<template>
  <div class="home">
    <div id="banner">
      <span class="headline1">Bauteilerkennung<span class="accent">.</span></span>
      <img height="400px" src="../assets/banner.svg" alt="">
    </div>
    <div class="text">
      <span>Um ein Bauteil erkennen zu lassen, mache ein Foto und lade es anschlie√üend hoch.</span>
    </div>
    <div id="buttonWrap">
      <!--<Button  v-on:click.native='showModalURL()' Title="Bild von URL" v-on:urlRecieved.native="changeURL($event)" />-->
      <Button  onclick="document.getElementById('fileInput').click()" Title="Mache ein Foto" />
      <input id="fileInput" type="file" v-on:change="onFileSelected" style="display: none"  accept="image/*" >
      <Button v-if="pictureURL!=''" Title="Erkenne Bild" @click.native="sendToBCS"/>
    </div>
    <div class="tagWrap" >
      <tag class="tag" v-for="tag in tags" :key="tag.value" :value="tag.value" :color="tag.color" />
    </div>
    <Picture id="picture" :url="pictureURL" />

    <Modal @hideModalURL="hideModalURL" @send-url="changeURL" v-if="ModalURL" />
    <spinner v-if="displaySpinner" />
  </div>
</template>

<script>
import axios from'axios'
import formdata from'form-data'
import * as fs from 'fs'

import Button from '@/components/Button.vue'
import Camera from '@/components/Camera.vue'
import Modal from '@/components/Modal.vue'
import Picture from '@/components/Picture.vue'
import tag from '@/components/tag.vue'
import spinner from '@/components/spinner.vue'

export default {
  name: 'Home',
  components: {
    Button,
    Camera,
    Modal,
    Picture,
    tag,
    spinner,
  },
  data(){
    return {
      video:{},
      canvas:{},
      captures:{},
      urlPrefix: 'https://patrec.azure-api.net',
      pictureURL:'',
      image:null,
      ModalURL:false,
      info:'',
      requestId:'id',
      displaySpinner:false,
      tagsColor:[
        {
          color: '#40D3DA',
        },
        {
          color: '#aad9db',
        },{
          color: '#c7dcdd'
        },
      ],
      tags:[
      ],
    }
  },
  methods: {
    changeURL(url) {
    this.pictureURL=url
    this.hideModalURL()
    },
    showModalURL(){
      this.ModalURL=true;
    },
    hideModalURL(){
      this.ModalURL=false;
    },
    onFileSelected(event) {
      this.image=event.target.files[0]
      var reader = new FileReader();
      reader.readAsDataURL(this.image);
      reader.onload=(event)=>{
        this.pictureURL=event.target.result;
      }
    },
    fillList(response){
      this.displaySpinner=false;
      console.log( response.data.results.length);
      var id='';
      for(var i=0; i < response.data.results.length; i++){
        id=response.data.results[i].id.replace(/_/g,' ');
        if(i<3){
          this.tags.push({value:id, color: this.tagsColor[i].color});
        }else{
          this.tags.push({value:id});
        }
      }
    },
    async sendToBCS(){
      this.displaySpinner=true;
      console.log(this.requestId);  
      var axios = require('axios');
      var FormData = require('form-data');
      var data = new FormData();
      data.append("image", this.image);

      this.info='loading';
      var vm=this;
  
      var config = {
        method: 'post',
        url: this.urlPrefix+'/api/v1/pipelines/5efb05ada04f9200011161fe/recognitions?pipeline_id=5efb05ada04f9200011161fe',
        headers: { 
          'Authorization': 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjVjMTEyNWFlNzJjYmZjMDAwNjRiMmE5MCIsInNhbHQiOiJENXJKMGVSbUxLUk01UGRTOHNyenphNUl6UU1xMnYrV2xnbSt0Tlo0aTJnPSIsInR5cGUiOiJyZWNvZ25pdGlvbiJ9.l6Ew3sMnIVSBHNtKL0iIKpbmKtLOBrFqgbetGpE8gXE'
        },
        data : data
      };
        console.log('send!AXIOS')
      axios(config)
      .then(function (response) {
        vm.info=response.data.id;
      })
      .catch(function (error) {
        console.log(error);
      });
      this.checkForInfo(1);
    },
    checkForInfo(type){
      if(this.info!='loading' && type===1){
        this.respondBCS();
      }else if(this.info!='loading' && type===2){
        this.fillList(this.info);
      }
      else{
        setTimeout(() => this.checkForInfo(type), 1000)
      }
    },
    respondBCS(){
      console.log(this.info);

      console.log('running!');
      var axios = require('axios');
      var config = {
        method: 'get',
        url: this.urlPrefix+'/api/v1/pipelines/5efb05ada04f9200011161fe/recognitions/' + this.info,
        headers: { 
          'Authorization': 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjVjMTEyNWFlNzJjYmZjMDAwNjRiMmE5MCIsInNhbHQiOiJENXJKMGVSbUxLUk01UGRTOHNyenphNUl6UU1xMnYrV2xnbSt0Tlo0aTJnPSIsInR5cGUiOiJyZWNvZ25pdGlvbiJ9.l6Ew3sMnIVSBHNtKL0iIKpbmKtLOBrFqgbetGpE8gXE'
        }
      };

      this.info='loading';
      var vm=this;
      
      axios(config)
      .then(function (response) {
        vm.info=response.data;
      })
      .catch(function (error) {
        console.log(error);
      });
      this.checkForInfo(2);
     
    },
  },
}
</script>

